<template>
<div id="detectioninfo">
    <div class="container">
        <div class="row">
            <div class="col-sm-8">
                <p>{{detectioninfo.msg}} </p>
            </div>
        </div>
    </div>
    <div class="container">
        <div class="row">
            <div class="col-sm-12">
                <div class="row">
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="icon-h center">
                            <img class="family" src="static/icon/family.png">
                        </div>
                        <router-link to="/detection/family" class="btn m10"></router-link>
                    </div>
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="icon-h center">
                            <img class="friend" src="static/icon/friend.png">
                        </div>
                        <router-link to="/detection/friend" class="btn m10"></router-link>
                    </div>
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="icon-h center">
                            <img class="love" src="static/icon/love.png">
                        </div>
                        <router-link to="/detection/love" class="btn m10"></router-link>
                    </div>
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="icon-h center">
                            <img class="work" src="static/icon/work.png">
                        </div>
                        <router-link to="/detection/work" class="btn m10"></router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DetectionFamily from '@/components/pages/DetectionFamily'
export default {
    name: "detectioninfo",
    data(){
        return{
            detectioninfo: {
                title: '',
                msg: ''
            }
        }
    }
}
</script>

<style>
#detectioninfo p{
    color: #fff;
}
.fas.fa-user-clock,.far.fa-list-alt,.fas.fa-cloud-rain{
    font-size: 100px;
    color: #fff;
    margin-top: 30px;
    margin-bottom: 50px;
}

.family{
    width: 200px;
    margin-bottom: 10px;
}
.love{
    width: 250px;
}
.friend{
    width: 200px;
}
.work{
    width: 180px;
    margin-bottom: 10px;
}
.m10{
    margin-top: 30px;
}

.icon-h{
    min-height: 250px;
}


</style>


